.skills {
  &_content {
    display: flex;
    padding: 20px;
    gap: 20px;

    &_inner-content {
      flex: 1;
      display: flex;
      flex-direction: column;
      align-items: center;

      &_catagory {
        display: inline-block;
        color: var(--yellow-theme-sub-text-color);
        opacity: 0.9;
        font-size: 22px;
        font-weight: 700;
        line-height: 1rem;
        letter-spacing: 0.5px;
        position: relative;
        text-transform: uppercase;
        padding: 5px 10px;
        border-bottom: 2px solid var(--yellow-theme-main-color);
      }

      &_catagory::before,
      &_catagory::after {
        position: absolute;
        content: "";
        width: 2px;
        height: 5px;
        background-color: var(--yellow-theme-main-color);
        bottom: 0px;
      }

      &_catagory::before {
        left: 0;
      }

      &_catagory::after {
        right: 0;
      }

      div {
        width: 100%;
      }

      .progressBar {
        padding-top: 50px;

        p {
          font-size: 1.6rem;
          margin: 0 0 10px;
          font-weight: 500;
          text-transform: uppercase;
          color: var(--yellow-theme-sub-text-color);
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .skills {
    &_content {
      flex-direction: column;
    }
  }
  .progressBar-container {
    margin-bottom: 20px;
  }
}
